import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms'; 
import { AuthenticationService } from '@app/_services';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.css']
})
export class AdminLoginComponent implements OnInit {

  adminLoginForm: FormGroup;
  
  constructor(private fb: FormBuilder, private login: AuthenticationService, public router: Router) { 
    this.loginForm();
    if(this.login.isLoggedIn() == true){
       this.router.navigate(['home']);
    }
  }

  loginForm(){
    this.adminLoginForm = this.fb.group({
      inputEmail: ['', [Validators.required, Validators.email]],
      inputPassword: ['', Validators.required] 
    });
  }

  adminLogin(inputEmail, inputPassword){
  	this.login.adminLogin(inputEmail, inputPassword)
    .pipe(first())
    .subscribe(data => {
      this.router.navigate(['home']);
    });
  }

  ngOnInit(): void {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('bg-dark');
  }

}
