import { Component, OnInit } from '@angular/core';
import { FormGroup,  FormBuilder,  Validators } from '@angular/forms';
import { BankService, AuthenticationService } from '@app/_services';
import { Router } from '@angular/router';
import { User } from '@app/_models';
import { first} from 'rxjs/operators';

@Component({
  selector: 'app-add-bank',
  templateUrl: './add-bank.component.html',
  styleUrls: ['./add-bank.component.css']
})
export class AddBankComponent implements OnInit {
  bankAddForm: FormGroup;	
  currentUser: User;

    constructor(
  	private fb: FormBuilder, 
  	private bankService: BankService, 
  	public router: Router, 
  	private authenticationService: AuthenticationService) { 
  	   this.bankForm();
  	   this.currentUser = this.authenticationService.currentUserValue;
    }

    bankForm(){
        this.bankAddForm = this.fb.group({
          name: ['', Validators.required],	
          walletAddress: ['', Validators.required],
          rgNumber: ['', Validators.required],
          email: ['', [Validators.required, Validators.email]],
          password: ['', Validators.required]
        });
    }

    addBank(name, wallet_address, rgNumber, email, password){
      console.log("name", name, wallet_address,rgNumber,email,password);
       this.bankService.addBank(name, wallet_address, rgNumber, email, password).pipe(first()).subscribe((res:any)=>{
        if(res.success ==  1){
            alert("Bank added successfully");
            this.router.navigate(['banks']);
        }
       },
      error =>{
          console.log(error);
          alert("This bank already exist");
      });
    }

  ngOnInit(): void {
  }

}
