import { Component, OnInit } from '@angular/core';
import { BankService, AuthenticationService } from '@app/_services';
import { ActivatedRoute, Router } from '@angular/router';
import { User, Bank } from '@app/_models';
import { first} from 'rxjs/operators';

@Component({
  selector: 'app-view-bank',
  templateUrl: './view-bank.component.html',
  styleUrls: ['./view-bank.component.css']
})
export class ViewBankComponent implements OnInit {
  currentUser: User;
  bankData: Bank;

  constructor(
  	private bankService: BankService,
  	private route: ActivatedRoute, 
    public router: Router, 
    private authenticationService: AuthenticationService
  	) { 
  	this.currentUser = this.authenticationService.currentUserValue;
  }

  ngOnInit(){
  	this.route.params.subscribe(params => { 
  		this.bankService.viewBank(params['walletAddress']).pipe(first()).subscribe((res: any)=>{
          if(res.success == 1){
          	this.bankData = res.data;
          }
  		}, 
  		error=>{
            console.log(error);
  		});
  	});
  }

}
