import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { environment } from '@environments/environment';
import { User } from '@app/_models';

@Injectable({ 
	providedIn: 'root' 
})

export class UserService{

    headers = new HttpHeaders().set('Content-Type', 'application/json');
	constructor(private http: HttpClient) { }

    getAll() {
        return this.http.get<User[]>(`${environment.apiUrl}/v1/users`);
    }

    getById(id: number) {
        //var d = new Date();
        return this.http.get<User>(`${environment.apiUrl}/v1/user/${id}`);
    }

    addWalletAddress(wallet_address: string, id: number): Observable<User>{
       return this.http.put<User>(`${environment.apiUrl}/v1/user/wallet-address`, {id: id, wallet_address: wallet_address });
    }
}