import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AdminLoginComponent } from './admin-login/admin-login.component';
import { AddBankComponent } from './add-bank/add-bank.component';
import { ListBankComponent } from './list-bank/list-bank.component';
import { ViewBankComponent } from './view-bank/view-bank.component';
import { AuthGuard } from '@app/_helpers';


const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: AdminLoginComponent },
  { path: 'home', component: HomeComponent, canActivate: [AuthGuard] },
  { path: 'banks/add', component: AddBankComponent, canActivate: [AuthGuard] },
  { path: 'banks', component: ListBankComponent, canActivate: [AuthGuard] },
  { path: 'banks/view/:walletAddress', component: ViewBankComponent, canActivate: [AuthGuard] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
